.contactList,.list,.item,.name,.number,.button{
   display: flex;
   flex-direction: row;
}
.list{
   flex-direction: column;
}
.item{
   align-items: flex-start; 
}
.name{
   margin: 0 10px 10px 0;
   width: 120px;
}
.number{
   margin: 0 10px 10px 0;
   width: 70px;
}
.button{
   border-radius: 5px;
   border: solid 1px;
   border-color: rgb(116, 116, 117);
   width: 60px;
}
.button:focus,.button:hover{
   background-color: rgb(81, 81, 251);
   box-shadow: 0px 0px 5px 5px rgba(255, 145, 0, 0.568);
   transition: box-shadow 500ms cubic-bezier(0.4, 0, 0.2, 1);
}